.footerproess{
  position: fixed;
  bottom: 10px;
}
.footerproess span{
  display: inline-block;
  width: 30px;
  height: 4px;
  background: #caceea;
  margin: 0 3px;
}
.footerproess span.active{
  background: #7c88da;
}
.loading{
  position: fixed;
  width: 100%;
  height: 100%;
  background: rgba(0,0,0,0.3);
  top: 0;
  left: 0;
  z-index: 9999;
}
.loading .load{
  position: fixed;
  top: 43%;
  left: 46%;
}


