.chat{
  width: 100%;
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
}
.chat-app_left {
  width: calc(35% - 1px);
  border-right: 1px solid grey;
  height: 100%;
  overflow: hidden;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}
.chat-app_left .chat-search{
  width: 100%;
  height: 62px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}
.chat-app_left .chat-search input{
  width: 90%;
  height: 34px;
  border-radius: 100px;
  border: 1px solid #ccc;
  padding-left: 10px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.chat-app_right {
  -ms-flex: 1 1;
      flex: 1 1;
  height: 100%;
  overflow: hidden;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}
.wrapper {
  height:  100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}

.search-bg {
  width:  505px;
  height:  122px;
  background-color: white;
  position:fixed; 
  left:0; 
  display:inline-block;
}

.chat-app {
  height: 100vh;
}



.heading {
  height: 60px;
  text-align: center;
  border-bottom: 1px solid rgba(0, 0, 0, .10);
}

/* thread */

.messenger-title {
  line-height: 60px;
  margin: 0;
}
.thread-list{
  -ms-flex: 1 1;
      flex: 1 1;
  overflow: auto;
  height: 100%;
}
.thread-list li{
  width: 100%;
  height: auto;
  display: block;
  border-bottom: 1px solid rgba(0, 0, 0, .10);
}
.thread-list li.active{
  background:rgba(242,242,242,1);
}
.thread-list li>a{
  display: block;
  width: 100%;
  margin-right: 0;
  border-bottom: 1px solid rgba(0, 0, 0, .10);
}

.thread-item {
  position: relative;
  height: 70px;
}

.thread-item_left {
  float: left;
  width: 50px;
  padding: 19px 10px;
}
.thread-item_left img{
  width: 40px;
  height: 40px;
}

.thread-item_right {
  float: left;
  width: calc(100% - 90px);
  padding: 20px 10px;
  position: relative;
}

.thread-from {
  font-weight: 400;
  line-height: 1.4em;
  color: black;
}

.thread-content {
  color: rgba(0, 0, 0, .40);
  font-size: 14px;
}

.thread-time {
  color: rgba(0, 0, 0, .40);
  position: absolute;
  top: 23px;
  right: 13px;
  font-size: 14px;
  width: 20px;
  height: 20px;
  background: #FF465D;
  color: white;
  border-radius: 50%;
  text-align: center;
  line-height: 20px;
}

.img-circle {
  border-radius: 50%;
}

/* message */

.current-target {
  line-height: 60px;
  font-size: 16px;
}

.message-list {
  -ms-flex: 1 1;
      flex: 1 1;
  overflow: auto;
}

.message-item {
  line-height: 2em;
  margin: 10px 0;
}

.chat-footer {
  border-top: 1px solid rgba(0, 0, 0, .10);
  height: 50px;
  background: white;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-align: center;
      align-items: center;
  padding: 0 10px;
  -ms-flex-negative: 0;
      flex-shrink: 0;
}
.chat-footer img{
  width: 20px;
  height: 20px;
}
.chat-footer input[type="text"]{
  -ms-flex: 1 1;
      flex: 1 1;
  margin-left: 10px;
  background:rgba(250,250,250,1);
  border-radius:100px;
  border:1px solid rgba(200,199,204,1);
  height: 30px;
}
.chat-footer label{
  width: 20px;
  height: 20px;
}
.chat-footer label input{
  width: 0;
  height: 0;
  display: none;
}

.message-from-other {
  padding-left: 25px;
  text-align: left;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: start;
      align-items: flex-start;
  -ms-flex-pack: center;
      justify-content: center;
}

.message-from-me {
  padding-right: 25px;
  text-align: left;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: end;
      align-items: flex-end;
  -ms-flex-pack: center;
      justify-content: center;
}

.message-from-other .mesgbom {
  display: inline-block;
  border-top-left-radius: 1.3em;
  border-top-right-radius: 1.3em;
  border-bottom-left-radius: 1.3em;
  border-bottom-right-radius: 1.3em;
  border: 1px solid rgba(0, 0, 0, .10);
  padding: 0 15px;
  color: black;
  background-color: rgba(255, 255, 255, .10);
}

.message-from-me .mesgbom {
  display: inline-block;
  border-top-left-radius: 1.3em;
  border-top-right-radius: 1.3em;
  border-bottom-left-radius: 1.3em;
  border-bottom-right-radius: 1.3em;
  padding: 0 15px;
  color: white;
  background-color: #0084ff;
}
.message-item  .content{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}
.message-item  .content .time{
  font-size: 12px;
  margin-top: -3px;
  color: #666;
}
.message-item  .content img{
  width: 100px;
  height: 100px;
  padding-top: 5px;
}
.message-from-other .content>img{
  width: 30px;
  height: 30px;
  margin-right: 5px;
  border-radius: 50%;
  padding: 0;
  margin-top: 13px;
}
.message-from-other .content .mesgtop{
  padding-left: 3px;
  font-size: 12px;
  color: #666;
}
.message-from-me .content>img{
  width: 0;
  height: 0;
  display: none;
  margin-right: 0px;
}
.new-message {
  width: 100%;
  height: 100%;
}

/* clearfix
 * http://stackoverflow.com/questions/211383/which-method-of-clearfix-is-best
 */
.clearfix:before,
.clearfix:after {
  content:"";
  display:table;
}
.clearfix:after {
  clear:both;
}
.clearfix {
  zoom:1; /* For IE 6/7 (trigger hasLayout) */
}



