body{
  overflow: hidden;
}
.container {
  width:  100%;
  height: auto;
  display: block;
  background-color: #f8f8f8;
}

.my-reward-list-title label {
	font-size: 24px;
}

.list-container {
	width: 100%;
	height: auto;
  overflow: auto;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.my-reward-list-title{
   margin-left: 0px;
   height: 120px;
   -ms-flex-align: center;
       align-items: center;
   display: -ms-flexbox;
   display: flex;
}

.my-reward-table {
  -ms-flex: 1 1;
      flex: 1 1;
  overflow: auto;
}
.my-reward-table>div{
  padding-bottom: 30px;
}
.top{
  display: -ms-flexbox;
  display: flex;
  -ms-flex: 1 1;
      flex: 1 1;
  -ms-flex-pack: justify;
      justify-content: space-between;
  padding: 0 30px;
}
.right{
  display: -ms-flexbox;
  display: flex;
}
.right .button{
  width: 120px;
  height: 30px;
  border-radius: 60px;
  background: #f3a536;
  color: white;
  -ms-flex-align: center;
      align-items: center;
  text-align: center;
  line-height: 30px;
  font-size: 14px;
}
.right img{
  width: 30px;
  height: 30px;
}
.top .right{
  width: 60%;
  -ms-flex-pack: end;
      justify-content: flex-end;
}
.top .right input{
  width: 100%;
  height: 37px;
  line-height: 37px;
  background: white;
  border-radius: 100px;
  border:1px solid #ccc;
  display: block;
  padding-left: 10px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}





