.container {
  width:  100%;
  height:  auto;
  display: block;
  background-color: #f8f8f8;
}

.my-quest-list-title label {
	font-size: 24px;
}
.quest .top .right>img{
  cursor: pointer;
}
.list-container {
	width: 100%;
	height:  100vh;
}

.my-quest-list-title{
    margin-left: 0px;
   height: 120px;
   -ms-flex-align: center;
       align-items: center;
   display: -ms-flexbox;
   display: flex;
}

.my-quest-table {
 -ms-flex: 1 1;
     flex: 1 1;
  overflow: auto;
}
.my-quest-table>div{
  padding-bottom: 30px;
}
.top{
  display: -ms-flexbox;
  display: flex;
  -ms-flex: 1 1;
      flex: 1 1;
  -ms-flex-pack: justify;
      justify-content: space-between;
  padding: 0 30px;
}
.right{
  display: -ms-flexbox;
  display: flex;
}
.right .button{
  width: 120px;
  height: 30px;
  border-radius: 60px;
  background: #f3a536;
  color: white;
  -ms-flex-align: center;
      align-items: center;
  text-align: center;
  line-height: 30px;
}
.right img{
  width: 30px;
  height: 30px;
}


