.mainheaderbar{
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: row;
	    flex-direction: row;
	height: 80px;
	background: white;
	-ms-flex-align: center;
	    align-items: center;
	padding: 20px 20px;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	margin-bottom: 4px;
	/* -moz-box-shadow:0px 2px 10px #333333; -webkit-box-shadow:0px 2px 10px #333333; box-shadow:0px 2px 10px #333333; */
}
.mainheaderbar .headerbarleft{
	width: 200px;
}
.mainheaderbar .headerbarleft img{
	width: 30px;
	height: 30px;
	border-radius: 50%;
}
.mainheaderbar .nav{
	-ms-flex: 1 1;
	    flex: 1 1;
	height: 100%;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: center;
	    justify-content: center;
}
.mainheaderbar .nav a{
	display: block;
	width: 100px;
	float: none;
	position: relative;
}
.mainheaderbar .nav a.active{
	color: #0A0D41;
}
.mainheaderbar .nav span.num{
	background: #FF465D;
	color: white;
	display: inline-block;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
	border-radius: 50%;
	padding: 0 7px;
	position: absolute;
	right: 10px;

}
.mainheaderbar .headerbarright{
	width: auto;
	-ms-flex-direction: row;
	    flex-direction: row;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
}
.mainheaderbar .headerbarright img{
	width: 30px;
	height: 30px;
	border-radius: 50%;
}
.mainheaderbar .headerbarright .user{
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
	cursor: pointer;
}
.mainheaderbar .headerbarright .user img{
	margin-left: 10px;
}
.mainheaderbar .headerbarright .language{
	margin-left: 20px;
	cursor: pointer;
}
.language{
	position: relative;
}
.language ul{
	position: absolute;
	width: 100%;
	display: none;
}
.language ul.active{
	display: block;
}
.language ul li{
	display: block;
	color:#666;
}
.language span{
	position: relative;
	padding-right: 10px;
}
.language span::after{
		content: '';
	  position: absolute;
		right: -10px;
    width: 7px;
    height: 7px;
		top: 3px;
    border-top: 2px solid #333;
    border-right: 2px solid #333;
    -webkit-transform: rotate(-135deg);
    -ms-transform: rotate(-135deg);
    transform: rotate(-225deg);
    cursor: pointer;
}


