.detailheader{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  padding: 0 30px;
  height: 50px;
  -ms-flex-align: center;
      align-items: center;
  border-bottom: 1px solid #ccc;
  -ms-flex-negative: 0;
      flex-shrink: 0;
}
.detailheader .headerleft{
  width: 50%;
}
.detailheader .headerright{
  width: auto;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}
.detailheader .headerright.hidden{
  display: none;
}
.detailheader .leftarrow {
  display: inline-block;
  width: 7px;
  height: 7px;
  border-top: 2px solid #333;
  border-right: 2px solid #333;
  -webkit-transform: rotate(-135deg);
      -ms-transform: rotate(-135deg);
          transform: rotate(-135deg);
  margin-right: 50px;
  cursor: pointer;
}
.detailheader .headerleft .status{
  color: #4A52D2;
  font-size: 14px;
  margin-left: 10px;
}
.detailheader  .headerright span{
  color: #9B9B9B;
  font-size: 14px;
}
.detailheader  .headerright .date{
  color: #0A0D41;
  margin-left: 10px
}
.detailheader  .headerright .appdate{
  margin-left: 20px;
}
.detailheader .btn{
  margin-right: 20px;
  width: 100px;
  height: 30px;
  line-height: 30px;
  background: #4A52D2;
  color: white;
  border-radius: 100px;
  text-align: center;
}
.subdate{
  display: inline-block;
}
.appdate{
  display: inline-block;
}


