.headerbar{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  padding: 0 30px;
  height: 50px;
  -ms-flex-align: center;
      align-items: center;
  border-bottom: 1px solid #ccc;
  -ms-flex-negative: 0;
      flex-shrink: 0;
}
.headerbar .left{
  -ms-flex:1 1;
      flex:1 1;
}
.headerbar .right{
  width: 120px;
  height: 30px;
  border-radius: 60px;
  background: #f3a536;
  color: white;
  -ms-flex-align: center;
      align-items: center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  line-height: 30px;
  position: relative;
  cursor: pointer;
}
.headerbar .right span+span{
  position: absolute;
  right: 10px;
  font-size: 16px;
}
.headerbar .leftarrow{
  position: relative;
}
.headerbar .leftarrow {
  display: inline-block;
  width: 7px;
  height: 7px;
  border-top: 2px solid #333;
  border-right: 2px solid #333;
  -webkit-transform: rotate(-135deg);
      -ms-transform: rotate(-135deg);
          transform: rotate(-135deg);
  margin-right: 50px;
  cursor: pointer;
}




